.navbar {
  background: transparent;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
  top: 50px;
  z-index: 999;
  transition: 0.5s;
}

.navbar.active {
  background-color: #0e0a1b;
  box-shadow: none;
  height: 80px;
  top: 0;
  transition: 0.5s;
}

.navbar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30px;
  max-width: 1500px;
}

.navbar-logo {
  color: #fff;
  justify-self: start;
  /* margin-left: 20px; */
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.fa-code {
  margin-left: 0.5rem;
  font-size: 1.8rem;
}
.fa-code:hover {
  opacity: 0.8;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 60vw;
  justify-content: end;
  margin-right: 2rem;
}

.nav-item {
  height: 80px;
  align-items: center;
  display: flex;
}

.nav-links {
  display: flex;
  color: #fff;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  height: 50%;
  cursor: pointer;
}

.nav-links:hover {
  border: 2px solid #fff;
  transition: all 0.2s ease-out;
  opacity: 0.8;
}

.fa-bars {
  color: #fff;
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #0e0a1b;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    background-color: #fff;
    color: #000000;
    border-radius: 0;
  }

  .navbar-logo {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(30%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }
  .fa-code {
    font-size: 2rem;
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: rgb(255, 255, 255);
    font-size: 2rem;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 1.5rem;
    background-color: transparent;
    color: #fff;
    padding: 14px 20px;
    border: 1px solid #fff;
    transition: all 0.3s ease-out;
  }

  .nav-links-mobile:hover {
    background: #fff;
    color: #242424;
    transition: 250ms;
  }
  @media screen and (max-height: 500px) {
    .navbar.active {
      font-size: 0.8rem;
    }
  }
}
